import React, { Component } from 'react';
import './App.scss';
import NumberForm from "./NumberForm";

class App extends Component {


    constructor(props) {
        super(props);
        this.state = { value: '' };
    }

    handleBase = (rawValue, base, sieveInvalidChars) => {
        const value = parseInt(rawValue, base);
        if (value > Number.MAX_SAFE_INTEGER) {
            alert(`${value} is beyond Number.MAX_SAFE_INTEGER (${Number.MAX_SAFE_INTEGER})`)
            return
        }

        const prohibitChars = sieveInvalidChars(rawValue);
        if (prohibitChars && prohibitChars.length > 0) {
            return
        }

        if (rawValue.length === 0) {
            return this.setState({ value: '' });
        }

        this.setState({ value });
    };

    validChar = (regex) => (asciiStr) => asciiStr.match(regex, '')
    handleChangeDeci = e => this.handleBase(e.target.value, 10, this.validChar(/[^\d]/g))
    handleChangeBin = e => this.handleBase(e.target.value, 2, this.validChar(/[^01]/g))
    handleChangeOct = e => this.handleBase(e.target.value, 8, this.validChar(/[^0-7]/g))
    handleChangeHex = e => this.handleBase(e.target.value, 16, this.validChar(/[^0-9a-fA-F]/g))


    decToDec = asciiDeci => asciiDeci
    decToBin = asciiDeci => asciiDeci.toString(2)
    decToOct = asciiDeci => asciiDeci.toString(8)
    decToHex = asciiDeci => asciiDeci.toString(16)


    render() {
        return (
            <div className="App">

                <NumberForm name="Decimal"
                    value={this.state.value}
                    onChange={this.handleChangeDeci}
                    unmarshalFromDecimal={this.decToDec}
                />
                <NumberForm name="Binary"
                    value={this.state.value}
                    onChange={this.handleChangeBin}
                    unmarshalFromDecimal={this.decToBin}
                />
                <NumberForm name="Octal"
                    value={this.state.value}
                    onChange={this.handleChangeOct}
                    unmarshalFromDecimal={this.decToOct}
                />
                <NumberForm name="Hexadecimal"
                    value={this.state.value}
                    onChange={this.handleChangeHex}
                    unmarshalFromDecimal={this.decToHex}
                />
                <div className="Footer">

                    <p>A simple tool that converts any of decimal, binary, octal and hexadecimal number into other
                        formats simultaneously.</p>
                    <p>Build by developer for developers and serves developer's need</p>
                    <div className="Footer__author">
                        <p>Brought to you by <i>Charnnarong Chomthiang</i></p>
                        <a href="https://github.com/Charnnarong/digitconverter">
                            <img
                                src="data:image/png;base64,"
                                alt="github" />
                        </a>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;
